svg {
  height: 50px;
  margin: 5px 10px;
  padding-right: 50px;
}

svg path {
  stroke-dasharray: 3300;
  stroke-dashoffset: 3300;
  -webkit-animation: draw 4s forwards, fill 2s 3s forwards;
          animation: draw 4s forwards, fill 2s 3s forwards;
  fill-opacity: 0;
  stroke: red;
  stroke-width: 8px;
}

@-webkit-keyframes draw {
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes draw {
  to {
    stroke-dashoffset: 0;
  }
}

@-webkit-keyframes fill {
  to {
    fill-opacity: 1;
    stroke-width: 5px;
  }
}

@keyframes fill {
  to {
    fill-opacity: 1;
    stroke-width: 5px;
  }
}
